.c-close {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 100; }
  .c-close a {
    color: #586e75; }
  .summary .c-close, .animate .c-close {
    display: none; }

.summary .c-mosaic {
  font-size: 0; }

.c-mosaic-titleshort {
  display: none; }
  .summary .c-mosaic-titleshort {
    display: block; }

.summary .c-mosaic-titlefull {
  display: none; }

.c-mosaic-title {
  text-transform: uppercase;
  color: #002b36;
  margin: 1.875rem 0; }

.summary .c-mosaic-content, .animate .c-mosaic-content {
  display: none; }

.c-mosaic-summary {
  display: none; }
  .summary .c-mosaic-summary, .animate .c-mosaic-summary {
    display: block; }

.summary .c-mosaic-item {
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  overflow: hidden;
  width: 50%;
  height: 12em; }
.animate .c-mosaic-item {
  position: absolute; }

@media (max-width: 47.9em) {
  .c-mosaic-subtitle {
    font-size: 1em; } }

.c-hello {
  min-height: 27em;
  position: relative; }

.c-hello-image {
  position: relative; }
  .c-hello-image img {
    background: #C4C4C4; }

.c-hello-image {
  width: 100%; }

.c-hello-content {
  padding: 0.9375rem; }
@media(min-width: 850px) {
  .c-hello-content {
    position: absolute;
    top: 10%;
    left: 55%; } }
@media print {
  .c-hello-link {
    display: none; }

  body {
    font-size: 14px; }

  .c-hello-image {
    display: none !important; }

  .c-hello {
    min-height: 0 !important; }

  .c-mosaic-title {
    margin: 0; }

  h2 {
    color: #93a1a1 !important;
    font-size: 1.5em !important;
    margin-bottom: 0.25em !important; }

  h3 {
    font-size: 1.25em !important;
    margin: 0; }

  p {
    margin: 0 0 0 1em; }

  body {
    font-size: 0.75em; }

  .container {
    max-width: 100% !important; }

  @page {
    size: 21cm 29.7cm;
    margin: 0 0 0 0; } }
